.card{
    border: 1px solid var(--secondary);
    border-radius: 5px;
    -webkit-transition-property: -webkit-transform, -webkit-box-shadow;
    transition-property: -webkit-transform, -webkit-box-shadow;
    -o-transition-property: transform, box-shadow;
    transition-property: transform, box-shadow;
    transition-property: transform, box-shadow, -webkit-transform, -webkit-box-shadow;
    -webkit-transition-duration: 250ms;
         -o-transition-duration: 250ms;
            transition-duration: 250ms;
    justify-self: center;
    -ms-flex-item-align: start;
        align-self: start;
    height: 360px;
    width: 240px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-bottom: 15px;
    background-color: var(--background);
}

.card:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    -webkit-box-shadow: 0 10px 25px var(--text-light);
            box-shadow: 0 10px 25px var(--text-light);
}


.c-logo{
    position: relative;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 40%;
    -ms-flex-negative: 1;
        flex-shrink: 1;
}

.c-info{
    width: 100%;
    height: 50%;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    padding:5px 10px;     
    line-height: 18px;
}

.c-name{
    margin: 0;
    color: var(--black);
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 500;
    position: relative;
}

.c-metrics{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin: 0;
    font-size: 14px;
    margin-bottom: 15px;
}

.c-profile{
    margin:0;
    color: var(--text-blue);
    font-weight: 500;
    margin-bottom: 5px;
    font-size: 14px;
    cursor: pointer;
}

.c-desc{
    margin: 0;
    color: var(--text-light);
    font-size: 14px;
}

.c-footer{
    width: 100%;
    height: 10%;
    display: -ms-flexbox;
    display: flex;
    color: var(--text-dark);
}

.likes{
    display: -ms-flexbox;
    display: flex;
    color: var(--red);
    font-size: 14px;
    margin-left: 15px;
    -ms-flex-align: center;
        align-items: center;
}

.likes>i{
    font-size: 14px;
}

.show-button, .favorite-button{
    width: 50%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
    font-size: 12px;
}

.show-button{
    background-color: var(--background-light);
}


.favorite-button{
    background-color: var(--primary);
}

.span{
    margin-left: 5px;
}


