.startups{
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.filter{
    height: 60px;
    background-color: var(--background-light);
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.startups-list{
    height: 100%;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    -ms-flex-pack: center;
        justify-content: center;
    overflow-y: auto;
    padding: 15px;
}

.startups-list.empty{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    color: var(--text-light);
}

.filter{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 15px;
}

.filter-profile{
    color: var(--text-blue);
    font-size: 14px;
    margin-right: 5px;
}

select{
    background-color: var(--background-light);
    border: none;
    outline: none;
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid;
}


